
import { getList } from 'api/article';
import { mapGetters } from 'vuex'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'detail',
data () {
return {
join_status: false,
community: '',
listQuery: {
page: 1,
limit: 10,
type: 'EOS',
id: '',
aspect: '',
},
list:  [],
total: null,
}
},
components: {
XButton,
},
created() {
this.$store.dispatch('InitJoin', '')
this.getList();
},
computed: {
...mapGetters([
'join',
])
},
methods: {
getList() {
this.community = this.join ? '' : ''
this.join_status = this.join
let id = this.$route.params.id;
this.listQuery.id = id;
getList(this.listQuery).then(response => {
this.list = response.data.items[0];

})
},
communityAdd () {
var that= this
if (!this.join_status) {
that.$store.dispatch('UpdateJoin', {join: true}).then(() => {
this.join_status = true
this.community = ''
this.$vux.toast.show({
type: 'success',
text: '',
})
})
}

}
}
}



